.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #fff);
  border-radius: 18px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  background-color: #fff;
  border: 1.5px solid #fff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
  box-shadow: inset 0 2px 2px #dadada;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  display: none;
}

